/*
* app form
*/
.ac-dlg {
    width: 100%;
    max-width: 800px;

    .ac-dlg-header {
        display: flex;
        align-items: center;

        img {
            width: 26px;
            height: 26px;
            margin-right: 10px;
        }
    }
    
    &.p-dialog.p-component .p-dialog-content {
        padding-top: 0;
        font-size: 0.85rem;
    }

    .ac-dlg-label {
        position: relative;
        font-size: 0.85rem;
        margin-bottom: 0;

        div {
            width: 100%;
            margin: 15px 0px 5px 0px;
        }

        span {
            display: flex;
        }

        &:focus-within {
            color: var(--ac-accent-color);
        }

        &.ac-invalid-input input {
            border: 2px solid var(--ac-danger-color);
            box-shadow: none !important;
        }

        &.ac-invalid-input + span.ac-invalid-input-msg {
            display: initial;
            color: var(--ac-danger-color); 
        }

        &.disabled {
            opacity: 0.7;
            width: auto;
        }
    }

    .ac-dlg-tooltip {
        padding: 0px 6px 2px 6px;
    }

    .ac-invalid-input-msg {
        display: none;
        font-size: 0.8rem;
    }

    .ac-required div:after {
        content: '*';
        margin-left: 5px;
        color: var(--ac-danger-color);
    }

    input, label, .p-dropdown.p-component {
        width: 100%;

        ::placeholder, .p-placeholder {
            opacity: 0.5;
        }

        ::-ms-clear {
            display: none;
        }
    }

    .p-inputtextarea.p-component {
        width: 100%;
        height: 70px;
        resize: none;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .ac-windowstype label {
        margin: 0px 40px 0px 5px;
    }

    .ac-error {
        display: none;
        align-items: center;
        height: 40px;
        margin-bottom: 10px;
        border: 1px solid var(--ac-danger-color);
        border-left: 5px solid var(--ac-danger-color);
        font-size: 0.8rem;

        &.visible {
            display: flex;
        }

        span {
            width: 18px;
            height: 18px;
            margin: 20px;
            background: url('../resources/icons/error.svg') 100% no-repeat;
        }
    }

    button[type=submit] {
        display: none;
    }
}

.ac-dropdown-badge {
    position: absolute;
    top: 48px;
    right: 35px;
    width: 24px;
    height: 24px;
    z-index: 1;

    &.running {
        background: url('../resources/icons/processing.svg') no-repeat;
        background-size: contain;
        animation: rotate 1s linear infinite;
    }
}
