/* HCL styles */
:root {
    --ac-accent-color: #0066B3;
    --ac-logo: url("../resources/images/HCL-logo.png");
}

/* ==========================================================================
HCL styles (copied from onetest-server)
========================================================================== */

$font-family-default: Roboto, Helvetica, sans-serif; /*ibm-plex-sans,Helvetica Neue,Arial,sans-serif;*/
$font-size-normal: 0.875rem;
$font-size-lower: 0.75rem;

$text-color: #363636;

$primary-color: #0066b3;
$primary-color-hover: lighten($primary-color, 10%);
$primary-color-pressed: #00518f;
$primary-color-inactive: #5a6872;

$primary-color-danger: #e91224;
$primary-color-hover-danger: #ec3544;
$primary-color-pressed-danger: #f84a59;
$primary-color-inactive-danger: #5a6872;
$secondary-color-danger: #5a6872;

$opacity-disabled: 0.5;
$secondary-color: #fff;
/*$tertiary-color: #5a6872;*/
$tertiary-color: #717171;
$input-background: #efefef;
$input-text-color: #152935;
$placeholder-color: #c6c6c6;
$checkbox-border-color: #5a6872;
$outline-focus-color: #a1c3fc;
$input-group-color: lighten($tertiary-color, 25%);
$line-color: #e2e2e2;
$secondary-line-color: #c6c6c6;

$dialog-border-top-color: $primary-color;
$dialog-background-color: $secondary-color;
$dialog-footer-background-color: #efefef;

$table-border-color: #efefef;
$primary-table-color: #383838;
$secondary-table-color: #5a6872;
$table-header-background: #003d6b;
$table-thead-background: #efefef;
$table-row-hover-background: rgba(85, 150, 230, 0.1);
$table-row-hover-border: #5596e6;

$toolbar-button-color: #0066b3;
$toolbar-button-hover-bg: #dfe3e6;
$toolbar-button-color-hover: lighten($toolbar-button-color, 25%);

$error-color: #e0182d;
$warning-color: #f9db00;
$info-color: #5aaafa;
$success-color: #78d62c;

$font-family-icon: 'Font Awesome 5 Free';
$font-family-icon-weight: 900;
$warning-icon: '\F071';
$error-icon: '\F057';
$info-icon: '\F05a';
$success-icon: '\F058';

$tooltip-color: white;
$tooltip-background-color: #717171;

$empty-alert-fg: #535353;
$empty-alert-bg: #efefef;

// From HCL Software Color Palette
$line-chart-color-1: #5ec1ef; // aqua
$line-chart-color-2: #eb1946; // ruby
$line-chart-color-3: #fab914; // gold
$line-chart-color-4: #bed732; // lime
$line-chart-color-5: #00afbe; // teal
$line-chart-color-6: #9173b5; // violet40
$line-chart-color-7: #38748f; // aqua70
$line-chart-color-8: #9c102e; // ruby70
$line-chart-color-9: #a67b0d; // gold70
$line-chart-color-10: #72811e; // lime70
$line-chart-color-11: #006972; // teal70
$line-chart-color-12: #482474; // violet60
