/*
* Add button
*/
body .act .p-splitbutton.p-component {

  > div:before {
    position: absolute;
    content: '';
    height: 7px;
    width: 7px;
    right: 11px;
    top: -5px;
    background-color: var(--ac-white-color);
    border-left: 1px solid var(--ac-secondary-border-color);
    border-top: 1px solid var(--ac-secondary-border-color);
    transform: rotate(45deg);
  }

  .p-button {
    display: flex;
    align-items: center;
    box-shadow: none;
    outline: none;
    background-color: var(--ac-accent-color);
    border: none;
    border-radius: 0;
    
    &:first-child {
      border-right: 1px solid var(--ac-white-color);

      .pi {
        font-size: 1.5em;
      }
    }

    .p-button-text {
      padding: 0px 1em 0px 1.5em;
    }

    &:hover {
      background-color: var(--ac-accent-color);
    }
  }

  .p-menu-overlay {
    width: max-content;
    right: 0;
    left: unset !important;
    top: 46px !important;
    text-align: initial;
    
    ul > li > a {
      padding: 10px 25px 10px 25px;
  
      &:hover {
        background-color: var(--ac-hover-primary-color);
      }
    }

    .p-menuitem-icon {
      background-repeat: no-repeat;
      width: 16px;
      height: 16px;
      margin-top: 1px;
    
      &.android {
        background-image: url('../icons/type/android.svg');
      }
      &.desktop {
        background-image: url('../icons/type/desktop.svg');
      }
      &.ios {
        background-image: url('../icons/type/ios.svg');
      }
      &.web {
        background-image: url('../icons/type/web.svg');
      }
    }
  }
}
