/*
* Confirm Dialog
*/
.ac-confirm-dlg {
    max-width: 800px;
    overflow-wrap: break-word;

    &.p-dialog.p-component {
        border-top: 4px solid var(--ac-danger-color); 

        .p-dialog-content {
            padding-top: 1rem;
            font-size: 0.85rem;

            div:not(:first-child) {
                display: flex;
                align-items: center;
                margin-top: 10px;
            }

            img {
                width: 16px;
                height: 16px;
                margin-right: 10px;
            }
            
            span:not(.ac-confirm-submsg) {
                font-weight: 600;
            }
        }


        .p-dialog-footer .p-button {

            &:enabled:hover,
            &:enabled:focus {
                outline-color: var(--ac-danger-color);
                border-color: var(--ac-danger-color);
            }

            &:first-child {
                color: var(--ac-tertiary-bg-color);
                border-color: var(--ac-tertiary-bg-color);

                &:enabled:hover,
                &:enabled:focus {
                    background-color: var(--ac-danger-color);
                    color: var(--ac-white-color);
                }
            }

            &:not(:first-child) {
                background-color: var(--ac-danger-color);
            }

            
        }

        .p-dialog-titlebar-close {
            box-shadow: none;
        }
    }
}