/*
* Associated Tests
*/
.acd-tests {
    display: flex;
    flex-grow: 1;

    .navigating {
        cursor: progress;
    }

    .acd-tests-header {
        display: flex;
        background-color: var(--ac-white-color);
        font-weight: 600;
        padding: 4px 12px 0px 12px;
    
        span:first-child {
            display: flex;
            flex-direction: row;
            align-items: center;
            flex-grow: 1;
            user-select: none;
            
            &.sortable {
                cursor: pointer;
                
                .pi {
                    padding-left: 3px;
                    
                    &.down:after {
                        content: "\E906";
                    }
        
                    &.up:after {
                        content: "\E907";
                    }
                }
            }

            &:not(.sortable) {
                font-weight: normal;
                font-size: 1.1em;
            }
        }
    
        .acd-propagate {
            display: flex;
            cursor: pointer;
            padding: 6px;
            border: 1px solid var(--ac-accent-color);
            border-radius: 2px;

            img {
                margin: 0;
            }

            &.disabled {
                cursor: initial;
                border: 1px solid var(--ac-primary-border-color);

                img {
                    filter: invert(76%) sepia(12%) saturate(0%) hue-rotate(235deg) brightness(91%) contrast(81%);
                }
            }

            &:not(.disabled):hover {
                background-color: var(--ac-hover-primary-color);
            }

            &.propagating {
                cursor: progress;
            }
        }

        > div.acd-loading {
            display: flex;
            font-weight: normal;
            font-size: 0.85rem;
            margin-top: 5px;

            > span:first-child {
                width: 24px;
                height: 24px;
                background: url('../resources/icons/processing.svg') center no-repeat;
                background-size: 24px;
                animation:rotate 1s linear infinite;
                margin-right: 5px;
            }
        }
    }

    .acd-tests-table {
        display: flex;
        flex-grow: 1;
        overflow: auto;
    
        .p-datatable.p-component {
            background-color: var(--ac-white-color);
            font-size: 0.8rem;
            overflow: auto;
            
            .p-datatable-wrapper table {
                border: none;
                margin-bottom: 10px;
        
                thead {
                    display: none;
                }
        
                tbody {
                    border: none;
        
                    > tr.p-datatable-row {
                        height: 20px;
                        cursor: initial;
                        background-color: var(--ac-white-color);

                        > td {
                            font-size: 0.85rem;
                            border: none;
                            padding: 0px 20px 0px 10px;
                            
                            &.acd-testpath {
                                padding-top: 4px;
                                
                                :hover:not(.navigating) {
                                    color: var(--ac-accent-color);
                                    text-decoration: underline;
                                    cursor: pointer;
                                }
                            }
        
                            .acd-testpath {
                                margin-right: 10px;
                            }
        
                            .p-checkbox {
                                -ms-transform: scale(0.8);
                                -webkit-transform: scale(0.8);
                                transform: scale(0.8);

                                .p-checkbox-icon {
                                    line-height: 1.1;
                                }

                                &.disabled {
                                    pointer-events: none;

                                    .p-checkbox-box {
                                        opacity: 0.3;
                                        cursor: initial;
                                        
                                        &:hover {
                                            border-color: var(--ac-primary-border-color);
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
