/* 
* Notification
*/
body .p-growl {
    &.p-growl-topright.top {
        top: 20px;
    }

    .p-growl-item-container .p-growl-item {
        background-color: var(--ac-white-color);
        border-left-color: var(--ac-success-color);
        color: var(--ac-tertiary-bg-color);
        
        .p-growl-title {
            font-weight: normal;
    
            span {
                font-weight: 600;
            }
        }
    
        button {
            color: var(--ac-primary-border-color);
        }
    }
}
