body .p-dropdown.p-component {

    .pi-chevron-down::before {
        content: "\E906";
    }

    display: inline-flex;
    align-items: center;
    border-radius: 0px;
         
    .p-dropdown-trigger {
        margin-left: 1rem;
        background: transparent;
    }
    .p-dropdown-label {
        background-color: transparent;
        font-family: 'Roboto', sans-serif;

        &.p-placeholder {
            color: $input-text-color;
            opacity: 0.7;
        }
    }

    position: relative;
    height: 2.5rem;
    max-height: 2.5rem;
    padding: 0 0.6rem;
    background-color: $input-background;
    border: none;
    box-shadow: inset 0 -1px 0 0 $tertiary-color;
    -webkit-box-ordinal-group: 2;
    cursor: pointer;
    color: $input-text-color;

    &:focus-within {
        box-shadow: inset 0 -2px 0 0 $primary-color;
    }

    .p-dropdown-panel {
       box-shadow: 0px 3px 3px 0 rgba(0, 0, 0, 0.1);
       background-color: $secondary-color;
    }

    .p-dropdown-header {
        background: none;
    }

    
    .p-dropdown-items > .p-dropdown-item {
       
      font-size: $font-size-normal;
      -webkit-box-align: center;
      height: 2.5rem;
      padding: 0 1rem;
      cursor: pointer;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      display: flex;
      align-items: center;
       
       
       &:hover {
           background-color: rgba(85, 150, 230, 0.1);
           outline: 1px solid transparent;
           color: $input-text-color;
           
           label {
              text-decoration: underline;
           }
       }

       &.p-highlight {
           background-color: $primary-color;
           color: #fff;
       }
    }

    .p-dropdown-filter-container {
        width: 100%;
        border-bottom: none;

        > .p-inputtext {
            border: 1px solid var(--ac-secondary-border-color);
            border-radius: 5px;
            background-color: var(--ac-white-color);  
            box-shadow: none !important;
        }

        .p-dropdown-filter-icon {
            color: #848484;
        }
    }
}
