/* 
* Progress bar
*/
.ac-pb {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
}

.ac-pb-text{ 
    text-align: center;
    color: var(--ac-primary-bg-color);
    margin: -50px 20px 20px 20px;
    font-size: 18px;
    opacity: 0.8;
}

.ac-pb-container {
    background-color: var(--ac-secondary-bg-color);
    text-align: center;
    width: 228px;
    height: 6px;
    border-radius: 5px;
}

.ac-pb-content {
    background: var(--ac-accent-color);
    height: 6px; 
    width: 80px;
    border-radius: 5px;
    animation: increase 3s infinite;
}

@keyframes increase {
    0% {transform: translateX(0);}
    50% {transform: translateX(148px);} 
    100% {transform: translateX(0);} 
}
