/* 
* App Configuration
*/
.ac {
  display: flex;
  position: relative;
  flex-direction: column;
  background-color: var(--ac-white-color);
  height: 100vh;
  min-width: 1000px;
}

header {
  display: flex;
  align-items: center;
  background-color: var(--ac-accent-color);
  min-height: 50px;
  z-index: 1;
}

header > span.ac-logo {
  background-image: var(--ac-logo);
  background-repeat: no-repeat;
  background-position: 20px 50%;
  height: 100%;
  width: 100%;
}

.ac-container {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
}

.ac-content {
  display: flex;
  flex-direction: column;
  min-width: 625px;
  width: 100%;
  margin: 20px;
}

.ac-title {
  display: flex;
  color: var(--ac-accent-color);
  font-weight: bold;
  font-size: 1.3em;
}

.ac-separator {
  background-color: var(--ac-secondary-bg-color);
  width: 100%;
  height: 1px;
  margin: 10px 0px 10px 0px;
}

.ac-viewer {
  display: flex;
  flex-direction: column;
  height: 100%;
}
