body .p-checkbox.p-component {

  &:focus-within:enabled:focus {
    outline: 4px solid $outline-focus-color;
  }


  .p-checkbox-box {
    width: 20px;
    height: 20px;

    border: 2px solid $checkbox-border-color;
    background-color: $secondary-color;
    border-radius: 0;

    .p-checkbox-icon {
      font-size: 0.9rem;
      line-height: 1.2;
    }

    &.p-disabled,
    &:disabled {
      opacity: $opacity-disabled;
    }

    &.p-highlight {
      background-color: $primary-color;
      border-color: $primary-color;

      border: 2px solid $checkbox-border-color;
      color: $secondary-color;

      // border: 0; //?? border already set above
      visibility: visible;
      white-space: nowrap;

      &.p-focus {
        outline: 2px solid $outline-focus-color;
      }
    }
  }
}

body .p-checkbox-label {
  font-family: $font-family-default;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: $font-size-normal;
  padding-left: 0.3rem;
  min-height: 1rem;
  margin-right: 1rem;
  margin-bottom: 0px;
}
