// Primereact styles overriding
body .p-tooltip.p-component {
  text-align: left;
  white-space: pre-wrap;
  font-size: 12px;
  max-width: 300px;

  > .p-tooltip-arrow {
    border-width: 4px;
  }
  &.p-tooltip-top > .p-tooltip-arrow {
    border-bottom-width: 0;
    border-top-color: $tooltip-background-color;
  }
  &.p-tooltip-right > .p-tooltip-arrow {
    border-left-width: 0;
    border-right-color: $tooltip-background-color;
  }
  &.p-tooltip-bottom > .p-tooltip-arrow {
    border-top-width: 0;
    border-bottom-color: $tooltip-background-color;
  }
  &.p-tooltip-left > .p-tooltip-arrow {
    border-right-width: 0;
    border-left-color: $tooltip-background-color;
  }
  > .p-tooltip-text {
    color: $tooltip-color;
    background-color: $tooltip-background-color;
  }
}

// And now the one from bootstrap 4 that can be set on any element but that we're missing a replacement for
$tooltip-arrow-height: 4px;
$tooltip-arrow-width: 8px;
.tooltip {
  &.show {
    opacity: 1;
  }
  &.bs-tooltip-top {
    padding: $tooltip-arrow-height 0;
    .arrow {
      bottom: 0;
      width: $tooltip-arrow-width;
      height: $tooltip-arrow-height;
      &::before {
        top: 0;
        border-width: $tooltip-arrow-height ($tooltip-arrow-width / 2) 0;
        border-top-color: $tooltip-background-color;
      }
    }
  }
  &.bs-tooltip-right {
    padding: 0 $tooltip-arrow-height;
    .arrow {
      left: 0;
      width: $tooltip-arrow-height;
      height: $tooltip-arrow-width;
      &::before {
        right: 0;
        border-width: ($tooltip-arrow-width / 2) $tooltip-arrow-height ($tooltip-arrow-width / 2) 0;
        border-right-color: $tooltip-background-color;
      }
    }
  }
  &.bs-tooltip-bottom {
    padding: $tooltip-arrow-height 0;
    .arrow {
      top: 0;
      width: $tooltip-arrow-width;
      height: $tooltip-arrow-height;
      &::before {
        bottom: 0;
        border-width: 0 ($tooltip-arrow-width / 2) $tooltip-arrow-height;
        border-bottom-color: $tooltip-background-color;
      }
    }
  }
  &.bs-tooltip-left {
    padding: 0 $tooltip-arrow-height;
    .arrow {
      right: 0;
      width: $tooltip-arrow-height;
      height: $tooltip-arrow-width;
      &::before {
        left: 0;
        border-width: ($tooltip-arrow-width / 2) 0 ($tooltip-arrow-width / 2) $tooltip-arrow-height;
        border-left-color: $tooltip-background-color;
      }
    }
  }
  .tooltip-inner {
    padding: 4px;
    color: $tooltip-color;
    background-color: $tooltip-background-color;
    border-radius: 3px;
    max-width: 220px;
  }
}

// Set className="larger-tooltip" to your reactstrap Tooltip component for the ones with long texts
.larger-tooltip > .tooltip-inner {
  max-width: 320px;
}

// Set className="much-larger-tooltip" to your reactstrap Tooltip component for the ones with long long texts
.much-larger-tooltip > .tooltip-inner {
  max-width: 480px;
}
