/*
* App File Selector
*/
.as {
    position: relative;

    input[type=file] {
        display: none;
    }

    .as-drop-area {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 145px;
        margin-top: 15px;
        border: 2px dashed var(--ac-accent-color);
        text-align: center;
        cursor: pointer;
        outline: none;

        &:focus-visible {
            background-color: var(--ac-hover-primary-color);
        }

        &.as-parsing {
            cursor: initial;
            
            > div.as-parsing-icon {
                width: 36px;
                height: 36px;
                background: url('../resources/icons/processing.svg') center no-repeat;
                background-size: 36px;
                animation:rotate 1s linear infinite;
            }
        }

        &.as-dragging {
            background-color: var(--ac-hover-primary-color);
        }

        > span:after {
            content: '*';
            margin-left: 5px;
            color: var(--ac-danger-color);
        }

        div {
            display: flex;
            align-items: center;
            max-width: 100%;
        }

        img {
            width: 48px;
            height: 48px;
            margin-bottom: 10px;
        }

        .as-app-id {
            overflow: hidden;
            text-overflow: ellipsis;
        }

        .as-cancel {
            position: absolute;
            top: 5px;
            right: 5px;

            > span {
                width: 32px;
                height: 32px;
                background: url('../resources/icons/erase.svg') center no-repeat;
    
                &:hover {
                    background-color: var(--ac-hover-primary-color);
                    border-radius: 5px;
                }
            }
        }
    }
}

@keyframes rotate{
    0%{transform:rotateZ(0deg)}
    100%{transform:rotateZ(360deg)}
}
