body .p-growl {
  opacity: 1;

  &.p-growl-topright {
    top: 70px;
    right: 20px;
  }

  .p-growl-item-container {
    .p-growl-item {
      background-color: #f5f5f5;
      border-left: solid 6px;

      .p-growl-image.pi::before {
        font-family: $font-family-icon;
        font-weight: $font-family-icon-weight;
        margin-right: 1rem;
        font-size: 0.6em;
      }

      .p-growl-image {
        top: 0px;
      }
    }

    &.p-growl-message-success .p-growl-item {
      border-left-color: $success-color;

      .p-growl-image {
        color: $success-color;
      }

      .p-growl-image.pi::before {
        content: $success-icon;
      }
    }

    &.p-growl-message-warn .p-growl-item {
      border-left-color: $warning-color;

      .p-growl-image {
        color: $warning-color;
      }

      .p-growl-image.pi::before {
        content: $warning-icon;
      }
    }

    &.p-growl-message-error .p-growl-item {
      border-left-color: $error-color;

      .p-growl-image {
        color: $error-color;
      }

      .p-growl-image.pi::before {
        content: $error-icon;
      }
    }

    &.p-growl-message-info .p-growl-item {
      border-left-color: $info-color;

      .p-growl-image {
        color: $info-color;
      }

      .p-growl-image.pi::before {
        content: $info-icon;
      }
    }
  }
}
