/*
*   App Toolbar
*/
.tb {
    background-color: var(--ac-primary-bg-color);
    min-width: 75px;
    text-align: center;
    z-index: 1;

    .tb-content {
        background-color: var(--ac-tertiary-bg-color);
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        border-left: 5px solid var(--ac-tertiary-border-color);
        height: 80px;
        padding-top: 6px;
    }

    .tb-button img {
        width: 24px;
        height: 24px;
    }
}
