.p-inputtext--base-definition {
  background-color: $input-background;
  border: none;
  box-shadow: inset 0 -1px 0 0 $tertiary-color;
  color: $input-text-color;
  font-family: $font-family-default;
  font-size: $font-size-normal;
  height: 2.5rem;
  padding: 0 1rem;
  -webkit-border-radius: 0px;
  border-radius: 0px;

  &:disabled {
    box-shadow: inset 0 -1px 0 0 $primary-color-inactive;
    cursor: not-allowed;
  }

  &:disabled:hover {
    border: none;
  }

  &:enabled:focus:not(.p-error) {
    outline: none;
    box-shadow: inset 0 -2px 0 0 $primary-color;
  }

  &::placeholder,
  &::-ms-input-placeholder {
    color: $placeholder-color;
    opacity: 1; // required by firefox
  }
}


body .p-inputtext.p-component {
  @extend .p-inputtext--base-definition;
}
