body .p-radiobutton.p-component {
  .p-radiobutton-box {
    width: 20px;
    height: 20px;

    border: 2px solid $checkbox-border-color;
    background-color: $secondary-color;
    border-radius: 10px;
    padding: 2px;

    &.p-focus {
      box-shadow: 0 0 10px $outline-focus-color;
    }

    &.p-disabled,
    &:disabled {
      opacity: $opacity-disabled;
    }

    &.p-highlight {
      border-color: $primary-color;

      border: 6px solid $primary-color;
      color: $primary-color;

      visibility: visible;
      white-space: nowrap;

      &.p-focus {
        box-shadow: 0 0 0 4px $outline-focus-color;
      }
    }
  }
}
