// Handles both primereact and reactstrap buttons
@mixin hclreact-p-button(
  $primary-color,
  $primary-color-hover,
  $primary-color-inactive,
  $primary-color-pressed,
  $secondary-color,
  $secondary-btn-color
) {
  background-color: $primary-color;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
  color: $secondary-color;
  font-family: $font-family-default;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 0;
  cursor: pointer;
  font-size: $font-size-normal;
  font-weight: 600;
  height: 2.5rem;
  padding: 0 1rem;
  border-radius: 0;
  text-align: center;
  text-decoration: none;
  transition-duration: 250ms;
  transition-timing-function: ease-in;
  white-space: nowrap;
  line-height: 16px;
  &:enabled:hover,
  &:enabled:focus {
    background-color: $primary-color-hover;
    outline-color: $secondary-color;
    color: $secondary-color;
    box-shadow: none; // 0 0 0 0.1em inset $primary-color;
  }
  &:enabled:hover {
    border-color: $primary-color-hover;
  }
  &.p-button-icon-only {
    > span.p-button-text {
      display: none;
    }
  }
  &.p-disabled:not(.p-button-icon-only),
  &:disabled:not(.p-button-icon-only) {
    background-color: $primary-color-inactive;
    color: $secondary-color;
  }
  &:enabled:focus {
    border: 2px solid $secondary-color;
    outline: 2px solid $primary-color-hover;
  }
  &:active {
    background-color: $primary-color-pressed;
  }
  &.p-button-secondary,
  &.btn-secondary {
    background-color: $secondary-color;
    border-width: 2px;
    border-style: solid;
    border-color: $secondary-btn-color;
    color: $secondary-btn-color;
    &:hover,
    &:focus {
      background-color: $primary-color-hover;
      color: $secondary-color;
    }
    &:focus {
      border: 2px solid $secondary-color;
      outline: 2px solid $primary-color-hover;
    }
    &:active {
      color: $primary-color-inactive;
    }
    &.p-disabled,
    &:disabled {
      color: $primary-color-inactive;
      border-color: $primary-color-inactive;
      background-color: transparent;
    }
  }
  // same as above but border & text color are red (primary)
  &.p-button-secondary-danger {
    background-color: transparent;
    border-width: 2px;
    border-style: solid;
    border-color: $primary-color;
    color: $primary-color;
    &:hover,
    &:focus {
      background-color: $primary-color-hover;
      color: $secondary-color;
    }
    &:focus {
      border: 2px solid $primary-color;
      outline: 2px solid $primary-color-hover;
    }
    &:active {
      color: $primary-color-inactive;
    }
    &.p-disabled,
    &:disabled {
      color: $primary-color-inactive;
      border-color: $primary-color-inactive;
      background-color: transparent;
    }
  }
}
body .p-button.p-component,
body .btn {
  @include hclreact-p-button(
    $primary-color,
    $primary-color-hover,
    $primary-color-inactive,
    $primary-color-pressed,
    $secondary-color,
    $primary-color
  );
}
body .p-button.p-button-danger,
body .p-buttonset.p-button-danger > .p-button {
  @include hclreact-p-button(
    $primary-color-danger,
    $primary-color-hover-danger,
    $primary-color-inactive-danger,
    $primary-color-pressed-danger,
    $secondary-color,
    $secondary-color-danger
  );
}
// reactstrap ButtonDropdown, dropdown part:
body .dropdown-menu {
