/* 
* Toggeable Fieldset
*/
body .p-fieldset {
    margin: 10px 0px 0px 0px;
    padding: 0px;
    border: none;

    .p-fieldset-legend {
        width: 100%;
    }

    &.p-fieldset-toggleable .p-fieldset-legend {
        margin: 0;
        
        a {
            display: flex;
            align-items: flex-end;
            flex-direction: row-reverse;
            background-color: var(--ac-white-color);
            color: var(--ac-accent-color);
            border: none;
            font-size: 14px;
            font-weight: initial;
            padding: 0px;
    
            &:hover {
                background-color: initial;
                color: var(--ac-accent-color);
                border: none;
                text-decoration: none;
            }
    
            &:focus {
                box-shadow: none;
            }
        }
    }

    .p-fieldset-toggler {
        &.pi-plus:before {
            color: var(--ac-accent-color);
            content: "\E906";
        }
        &.pi-minus:before {
            color: var(--ac-accent-color);
            content: "\E907";
        }
    }
}
