body .p-datatable.p-component {
  .p-datatable-header {
    background-color: transparent;
    padding: 0.7rem 0rem;
    border: none;
  }

  .p-datatable-wrapper > table {
    border-bottom: 1px solid $table-border-color;
    border-collapse: collapse;
    border-spacing: 0;
  }

  tr.p-datatable-row:hover,
  tr.p-datatable-row.p-highlight:hover,
  tr.p-datatable-row:nth-child(even):hover,
  tr.p-datatable-row:nth-child(even).p-highlight .p-row-toggler:hover,
  &.p-datatable-hoverable-rows .p-datatable-tbody > tr:not(.p-highlight):not(.p-highlight-contextmenu):hover {
    background: $table-row-hover-background;
  }

  tr.p-datatable-row.p-highlight:focus,
  tr.p-datatable-row:focus:not(.p-highlight),
  tr.p-datatable-row:focus,
  tr.p-highlight:focus,
  tr.p-datatable-row:focus-within,
  tr.p-datatable-row:nth-child(even):focus:not(.p-highlight) {
    /*outline: solid 1px $table-row-hover-border;*/
    background: $table-row-hover-background;
  }

  tr.p-datatable-row.p-highlight:not(:focus):not(:hover):not(:focus-within),
  tr.p-datatable-row:nth-child(even):not(:focus):not(:hover):not(:focus-within) {
    background: none;
  }

  .p-datatable-tbody > tr.p-highlight .p-row-toggler {
    background: none;
    color: $tertiary-color;
  }

  .p-sortable-column:focus {
    box-shadow: inset 0 0 0 1px $primary-color;
  }

  &.p-datatable-auto-layout > .p-datatable-wrapper {
    overflow-x: initial;
  }

  .p-datatable-tbody > tr.p-datatable-row {
    color: $primary-table-color;
    > td {
      border-right: none;
      .p-row-toggler {
        margin: -2px -2px -4px -2px;
        padding: 2px 2px 4px 2px;
        border-radius: 0;
        &:focus {
          box-shadow: inset 0 0 0 2px $primary-color;
        }
      }
    }

    > td:not(:first-child) {
      border-left: none;
    }
  }

  .p-datatable-thead {
    padding: 0.7rem 0rem;
    background-color: $table-thead-background;
    color: $primary-table-color;
    font-weight: bold;

    tr > th.p-sortable-column.p-highlight th.p-sortable-colum .p-sortable-column-icon {
      color: inherit;
      font-size: 14px;
    }

    tr > th:not(:first-child) {
      border-left: solid 1px $table-border-color;
    }

    .p-sortable-column-icon.pi.pi-sort-down {
      position: relative;
      top: -3px;
    }

    .p-sortable-column-icon.pi.pi-sort-up {
      position: relative;
      top: 3px;
    }

    tr > th {
      color: $primary-table-color;
      background-color: $table-border-color;
      border-color: $table-border-color;
      text-align: left;
      font-size: 0.875rem;
      height: 48px;
      font-weight: normal;

      &.p-sortable-column .p-sortable-column-icon {
        visibility: hidden;
        font-size: 14px;
      }

      &.p-sortable-column:hover .p-sortable-column-icon {
        visibility: visible;
      }

      &.p-sortable-column.p-highlight {
        background-color: $table-thead-background;
        color: initial;
        .p-sortable-column-icon {
          visibility: visible;
          color: initial;
        }
      }
    }

    tr > th:not(:last-child) {
      border-left: none;
      border-right: none;
    }

    tr > th:first-child {
      border-left: 1px solid $table-border-color;
    }

    tr > th:last-child {
      border-left: none;
    }
  }

  .p-datatable-tbody {
    background-color: $secondary-color;
    border-right: 1px solid $table-border-color;
    font-size: $font-size-normal;
    color: $primary-table-color;

    > tr {
      height: 3rem;

      > td:first-of-type,
      > th:first-of-type {
        border-left: 1px solid $table-border-color;
      }

      > td {
        border-color: $table-border-color;
        padding: 0.429em 0.857em;
        font-size: 1rem;

        .secondary-information {
          color: $secondary-table-color;
          font-size: 0.8rem;
          font-weight: normal;
        }
      }
    }
  }

  .p-datatable-even {
    background-color: $table-thead-background;
  }

  .p-paginator {
    border-color: $table-border-color;
    background-color: $table-thead-background;

    .p-dropdown {
      background-color: transparent;
    }
  }
}
