.ais {
    position: relative;
    justify-content: center;
    align-items: center;
    min-width: 40px;
    border: 1px solid var(--ac-primary-border-color);
    cursor: pointer;
    margin-left: 10px;
    outline: none;

    &:hover, &:focus-visible {
        border: 2px solid var(--ac-accent-color);

        > span {
            top: -12px;
            right: -12px;
            
            &:not(.selected) {
                background-color: var(--ac-accent-color);
                border: 1px solid var(--ac-accent-color);
    
                span {
                    background: url('../resources/icons/select.svg');
                    background-size: contain;
                }
            }
        }
    }

    img {
        width: 24px;
        height: 24px;
    }

    .ais-badge {
        position: absolute;
        top: -11px;
        right: -11px;
        background-color: var(--ac-white-color);
        border: 1px solid var(--ac-primary-border-color);
        border-radius: 50%;
        padding: 2px;
        cursor: pointer;
        outline: none;

        span {
            background: url('../resources/icons/accent/select.svg');
            background-size: contain;
            width: 14px;
            height: 14px;

            &.selected {
                background: url('../resources/icons/accent/close.svg');
            }
        }
        
        &:hover, &:focus-visible {
            background-color: var(--ac-accent-color);
            border: 1px solid var(--ac-accent-color);

            span {
                background: url('../resources/icons/select.svg');
                background-size: contain;
    
                &.selected {
                    background: url('../resources/icons/close.svg');
                }
            }
        }
    }

    input[type=file] {
        display: none;
    }
}
