body .p-component-overlay.p-dialog-mask {
  opacity: 1;
  transition: opacity 200ms;
  visibility: visible;
  background-color: rgba(0, 0, 0, 0.5);
}

body .p-dialog.p-component {
  box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.1);
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  background-color: $dialog-background-color;
  border-top: 4px solid $dialog-border-top-color;
  overflow-y: auto;
  overflow-x: hidden;

  .p-dialog-titlebar-close-icon {
    font-size: 1.25rem;
  }

  .p-dialog-titlebar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: $dialog-background-color;
    border-bottom: none;
    padding: 0.5rem 1.5rem 1.5rem 1.5rem;
    > .p-dialog-title {
      padding-top: 2rem;
      padding-left: 0.5rem;
      font-size: 1.75rem;
      font-weight: bold;
      color: $primary-table-color;
      white-space: normal;
      word-break: break-word;
    }

    .p-dialog-titlebar-icon.p-dialog-titlebar-close {
      color: $secondary-table-color;
      font-size: 1.6rem;
      font-weight: 100;
    }

    .p-dialog-titlebar-icons {
      position: absolute;
      right: 8px;
      top: 8px;
    }
  }

  .p-dialog-content {
    font-size: 1rem;
    line-height: 1.5;
    font-weight: 400;
    padding: 2rem;
    color: $primary-table-color;
    border-top: none;
    border-bottom: none;
  }

  .p-dialog-footer {
    border-top: none;
    background-color: $dialog-footer-background-color;
    padding: 1.5rem 2rem;

    .p-button:not(:last-child) {
      margin-right: 1rem;
    }
  }

  .p-resizable-handle::before {
    content: '\E90B';
  }
}

.p-dialog-right-position {
  transform: none; // This is how we ensure our dialogs are not moved by the animation...
  top: 0;
  bottom: 0;
  left: auto;
  right: 0;
  // Fix new issue with primereact 3.4.0 where the dialog finally migrates to the center of the viewport
  &.p-dialog-enter-done {
    position: fixed;
    top: 0;
    bottom: 0;
    left: auto;
    right: 0;
  }
}

/* Distinguish right dialog width policies depending on browser window width: */

@media only screen and (max-width: 768px) {
  body .p-dialog.p-dialog-right-position {
    width: 100%;
  }
}

@media only screen and (min-width: 769px) and (max-width: 992px) {
  body .p-dialog.p-dialog-right-position {
    width: 75%;
  }
}

@media only screen and (min-width: 993px) and (max-width: 1200px) {
  body .p-dialog.p-dialog-right-position {
    width: 50%;
  }
}

@media only screen and (min-width: 1201px) {
  body .p-dialog.p-dialog-right-position {
    width: 600px;
  }
}
