/*
* app details
*/
.acd {
    display: flex;
    position: relative;
    font-size: 0.8em;
    min-height: 200px;

    .acd-content {
        display: grid;
        grid-template-columns: 50% auto;
        column-gap: 30px;
        flex-basis: 100%;
        background-color: var(--ac-secondary-bg-color);
        color: var(--ac-black-color);
        margin: 0px 10px 0px 55px;
        padding: 20px;
        line-height: 1.7;

        span {
            flex-direction: column;
            overflow: hidden;
            text-overflow: ellipsis;
            
            :not(.acd-description) {
                word-break: break-all;
            }

            .acd-description {
                white-space: pre-wrap;
            }
        }
    }
    
    img {
        width: 16px;
        height: 16px;
        margin-right: 10px;
    }

    .acd-verdict {
        position: absolute;
        border-left: 4px solid var(--ac-secondary-border-color);
        top: -47px;
        left: 34px;
        @-moz-document url-prefix() {
            top: -50px;
            left: 35px;
        }
    }

    .acd-label {
        color: var(--ac-tertiary-bg-color);
        font-weight: 500;
        opacity: 0.9;
    }

    .acd-main-value {
        display: flex;
        align-items: center;
        font-weight: 700;
    }
    
    .acd-separator {
        height: 20px;
    }

    .acd-subst-value {
        filter: brightness(85%);
    }
}

.acd-debug {
    white-space: pre-wrap !important;
    
    &:not(span) {
        padding: 30px;
    }
    
    > div.p-button {
        height: auto;
        margin-bottom: 10px;
        padding: 0;
        box-shadow: none;
    }
}
