:root {
  /* To be merged with _vars.scss*/
  --ac-primary-bg-color: #383838;
  --ac-secondary-bg-color: #EFEFEF;
  --ac-tertiary-bg-color: #555555;
  --ac-primary-border-color: #ABABAB;
  --ac-secondary-border-color: #C6C6C6;
  --ac-tertiary-border-color: #087DD7;
  --ac-white-color: white;
  --ac-black-color: black;
  --ac-hover-primary-color: rgba(85,150,230,0.2);
  --ac-hover-secondary-color: #dcebfa;
  --ac-danger-color: #E0182D;
  --ac-success-color: #5AA700;
}

body {
  margin: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root div::-webkit-scrollbar {
  width: 7px;
  height:7px;
}
:root div::-webkit-scrollbar-thumb {
  background-color: var(--ac-primary-border-color);
}
:root div::-webkit-scrollbar-track {
  background-color: var(--ac-white-color);
}

:root div {
  scrollbar-color: var(--ac-primary-border-color) var(--ac-white-color);
  scrollbar-width: thin;
}

@keyframes rotate{
  0%{transform:rotateZ(0deg)}
  100%{transform:rotateZ(360deg)}
}
